import $ from 'jquery';

function scrollNav(target, offset) {
  var _offset = offset;
  $('html, body').animate({
    scrollTop: $(target).offset().top - _offset + 'px',
  }, 400);
  return this;
}

function scrollNavs(nav) {
  if (nav) {
    for (let i = 0; i < nav.length; i++) {
      $('#' + nav[i].id).click(function (e) {
        let url = $(this).attr('href');
        e.preventDefault();
        scrollNav(url, 0);
      });
    }
  }
}

function transElement(option) {
  var defaults = {
    run: true,
    anchorPoint: null,
    target: null,
    inStyle: null,
    outStyle: null,
    offset: 0,
  };

  var settings = $.extend({}, defaults, option);
  var init = function () {
    if (settings.run) {
      if (settings.anchorPoint !== null && settings.target !== null) {
        transElem(settings.anchorPoint, settings.target, settings.inStyle, settings.outStyle, settings.offset);
      } else {
        console.error('Targe not found.');
      }
    }
  };

  var transElem = function (anchorpoint, target, instyle, outstyle, offset) {
    $(document).scroll(function () {
      var flag = $(anchorpoint).offset().top;
      var scrollTop = $(this).scrollTop();
      var winHeight = 0;

      if (offset) {
        winHeight = $(window).height() * offset;
      }

      var _scrollTop = scrollTop + winHeight;
      if (flag !== undefined) {
        if (_scrollTop > flag) {
          $(target).css('visibility', 'visible');
          if ($(target).hasClass(outstyle)) {
            $(target).removeClass(outstyle);
          }

          $(target).addClass(instyle);
        } else {
          if (outstyle !== null) {
            $(target).removeClass(instyle);
            $(target).addClass(outstyle);
          } else {
            $(target).removeClass(instyle);
          }
        }
      } else {
        console.error('Flag is undefined.');
      }
    });
  };

  init();
}

function transElements(arr) {
  if (arr) {
    for (var i = 0; i < arr.length; i++) {
      $(arr[i][1]).css('visibility', 'hidden');
      transElement({
        anchorPoint: arr[i][0],
        target: arr[i][1],
        inStyle: arr[i][2] + ' animated',
        outStyle: arr[i][3] + ' animated',
        offset: arr[i][4] || 0,
      });
    };
  }
}

function modal() {
  $('#modal').click(function (event) {
    event.preventDefault();
    $('#modal').removeClass('show-modal');
    $('#modal-container').removeClass('show-modal-container');
    $('body').css('overflow', 'auto');
    $('#modal-container-img').css('background-image', '');
    $('#modal-container-text-items').html('');
  });

  $('.thumblink').click(function (event) {
    event.preventDefault();
    var imgFile = $(this).data('img');
    $('#modal-container-img').css('background-image', 'url(' + imgFile + ')');
    var categoryName = $(this).data('category');
    $('#modal-container-text-items').append('<h5>' + categoryName + '</h5>');
    var menuArray = $(this).data('menu');
    if (menuArray) {
      for (let i = 0; i < menuArray.length; i++) {
        $('#modal-container-text-items').append('<div><h6>' + menuArray[i].name + '</h6><p>' + menuArray[i].ingredients + '</p></div>');
      }
    }

    var menuDescription = $(this).data('description');
    if (menuDescription) {
      $('#modal-container-text-items').append('<div><h6>' + menuDescription + '</h6></div>');
    }

    $('body').css('overflow', 'hidden');
    $('#modal').addClass('show-modal');
    $('#modal-container').addClass('show-modal-container');
  });
}

module.exports = {
  scrollNav: scrollNav,
  scrollNavs: scrollNavs,
  transElement: transElement,
  transElements: transElements,
  modal: modal,
};



