import './css/style.sass';
import _ from 'underscore';
import $ from 'jquery';
import dom from './js/dom';
import provider from './js/provider';
import layout from './templates/layout.handlebars';
import page from './templates/page.handlebars';

const contentIdonLayoutTemplate = 'contentIdonLayoutTemplate';

provider.get('./db/info.json').then(function (res) {
  var data = JSON.parse(res);
  data.contentIdonLayoutTemplate = contentIdonLayoutTemplate;
  return data;
}).then(function (info) {
  return provider.get('./db/content.json').then(function (res) {
    var data = {};
    data.info = info;
    data.content = JSON.parse(res);
    function stringifyItems(arr) {
      for (let i = 0; i < arr.length; i++) {
        arr[i].stringify = JSON.stringify(arr[i].items);
      }
    }

    stringifyItems(data.content.sections.menu.categories);
    return data;
  });
}).then(function (data) {
  $('body').html(layout(data));
  $('#' + contentIdonLayoutTemplate).html(page(data));
  return data;
}).then(function (data) {
  dom.scrollNavs(data.content.nav);

  setTimeout(function () {
    $('#face-big-logo').css('visibility', 'visible').addClass('fadeInUp' + ' animated');
  }, 1500);

  var transElementsArr = [
    ['#about-us', '#about-us-text', 'fadeInLeft', false, 0.8],
    ['#about-us', '#about-us-image', 'fadeInRight', false, 0.6],
    ['#menu', '#menu-panini-menu', 'fadeIn', 'fadeOut', 0.9],
    ['#menu', '#menu-egg-select-menu', 'fadeIn', 'fadeOut', 0.8],
    ['#menu', '#menu-salad-menu', 'fadeIn', 'fadeOut', 0.7],
    ['#menu', '#menu-waffles-menu', 'fadeIn', 'fadeOut', 0.6],
    ['#menu', '#menu-sweet-tooth-menu', 'fadeIn', 'fadeOut', 0.5],
    ['#menu', '#menu-specialties-menu', 'fadeIn', 'fadeOut', 0.4],
    ['#coffees', '#coffees-typo', 'fadeIn', 'fadeOut', 0.9],
    ['#coffees', '#ethiopia-coffees', 'fadeIn', 'fadeOut', 0.8],
    ['#coffees', '#guatemala-coffees', 'fadeIn', 'fadeOut', 0.7],
    ['#coffees', '#indonesia-coffees', 'fadeIn', 'fadeOut', 0.6],
    ['#coffees', '#brazil-coffees', 'fadeIn', 'fadeOut', 0.5],
    ['#coffees', '#columbia-coffees', 'fadeIn', 'fadeOut', 0.4],
    ['#other-beverages', '#other-beverages-image01', 'fadeInLeft', false, 0.9],
    ['#other-beverages', '#other-beverages-image02', 'fadeInLeft', false, 0.6],
    ['#other-beverages', '#other-beverages-text', 'fadeInRight', false, 0.5],
    ['#contact-us', '#contact-us-heading', 'fadeInDown', 'fadeOutUp', 0.8],
    ['#contact-us', '#contact-us-address', 'fadeInDown', 'fadeOutUp', 0.7],
    ['#contact-us', '#contact-us-telephone', 'fadeInDown', 'fadeOutUp', 0.6],
    ['#contact-us', '#contact-us-web', 'fadeInDown', 'fadeOutUp', 0.5],
    ['#contact-us', '#contact-us-sns', 'fadeInDown', 'fadeOutUp', 0.4],
  ];
  dom.transElements(transElementsArr);

  $('.section-menu-btn').hover(function () {
    $(this).parent().addClass('active');
  }, function () {
    $(this).parent().removeClass('active');
  });

  dom.modal();
});



