module.exports = function request(href, callback) {
  var req = new XMLHttpRequest();
  req.open('GET', href, true);
  req.onload = function () {
    if (req.status >= 200 && req.status < 400) {
      callback(null, req.response);
    } else {
      callback(req.statusText, null);
    }
  };

  req.onerror = function () {
    callback(req.statusText, null);
  };

  req.send(null);
};



